import React from 'react';
import PropTypes from 'prop-types';
import uniqid from 'uniqid';

import Event from '../event/event';
import Loading from '../loading/loading';
import RequestField from '../requestFailed/requestFailed';
import { ITEMS_STATUS_PARAMS } from '../../constants/constants';
import './eventList.scss';

class EventList extends React.Component {
  componentDidMount() {
    const { fetchData } = this.props;
    fetchData();
  }

  render() {
    const {
      events,
      fetchStatus,
    } = this.props;

    switch (fetchStatus) {
      case ITEMS_STATUS_PARAMS.ITEMS_HAS_ERRORED: // fetch failed
        return <RequestField />;

      case ITEMS_STATUS_PARAMS.ITEMS_IS_LOADING: // loading
        return <Loading />;

      default: // fetch success
        if (Object.keys(events).length > 0) {
          return (
            <div className="events-section__event-list-container" id="event-list">
              <div
                className="events-section__day-events"
              >
                {Object.keys(events).map(eventDay => (
                  <>
                    <h5 className="events-section__day-title">
                      {events[eventDay].date}
                    </h5>
                    <div className="events-section__day-events-list" key={uniqid()}>
                      {Object.keys(events[eventDay].items)
                        .map(item => (
                          <Event
                            key={uniqid()}
                            event={events[eventDay].items[item]}
                          />
                        ))}
                    </div>
                  </>
                ))}
              </div>
            </div>
          );
        }
        return (
          <div className="events-section__event-list-container" id="event-list" />
        );
    }
  }
}

EventList.propTypes = {
  fetchData: PropTypes.func.isRequired,
  fetchStatus: PropTypes.string.isRequired,
  // eslint-disable-next-line react/forbid-prop-types
  events: PropTypes.object.isRequired,
};

export default EventList;
