/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React from 'react';
import PropTypes from 'prop-types';
import Parser from 'html-react-parser';
import cn from 'classnames';
import uniqid from 'uniqid';

import Checkbox from '../checkbox/checkbox';
import saveEvents from '../../api/saveEvents';
import replaceAll from '../../api/replaceAll';


import './event.scss';

class Event extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isFocudsed: false,
    };
    this.focusElement = this.focusElement.bind(this);
  }

  focusElement() {
    this.setState(prevState => ({
      isFocudsed: !prevState.isFocudsed,
    }));
    const { isFocudsed } = this.state;
    if (!isFocudsed) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'scroll';
    }
  }

  render() {
    const { event } = this.props;
    const {
      img, schedule, title, link,
      id, description, checked,
    } = event;
    const { isFocudsed } = this.state;

    return (
      <div className="event-section">
        <div className="event-section__event-day_container">
          {Object.keys(schedule).map(eventDay => (
            <div className="event-section__event-day">
              <div className="events-day__event-checkbox">
                <Checkbox label="Выбрать" isSaved={checked} id={id} handleCheckboxChange={saveEvents} />
              </div>
              <div className="event-day__event-info">
                <p className="event-info__start-time">{schedule[eventDay].timeStart}</p>
                <p className="event-info__content">{schedule[eventDay].point}</p>
              </div>
            </div>
          ))}
        </div>
        <div className="event-section__event-day_container">
          <div
            className="event-day_container__main-info"
          >
            <h3 className="main-info__event-title">{Parser(title)}</h3>
            <span
              onClick={this.focusElement}
              className="more-info__link"
            >
              ...more
            </span>
          </div>
        </div>
        <div className={cn({
          visible: isFocudsed,
          popup: true,
        })}
        >
          <div className="popup-content">
            <span className="close" onClick={this.focusElement}>&times;</span>
            <img
              className="popup-content__img"
              src={`http://spravafestival.by/${img}`}
              alt="artist or group"
            />
            <p className="popup-content__description">
              {Parser(description)}
              {' '}
              <a href={link} className="popup-content__link">{link}</a>
            </p>
          </div>
        </div>
      </div>
    );
  }
}

Event.propTypes = {
  event: PropTypes.objectOf(PropTypes.shape({
    img: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    schedule: PropTypes.objectOf(PropTypes.shape({
      point: PropTypes.string.isRequired,
      timeStart: PropTypes.string.isRequired,
      timeEnd: PropTypes.string.isRequired,
    })).isRequired,
    checked: PropTypes.bool.isRequired,
    description: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    isEventSave: PropTypes.bool.isRequired,
    id: PropTypes.string.isRequired,
  })).isRequired,
};

export default Event;
